@media screen and (max-width: 768px) {
  /* 基础样式调整 */
  body {
    font-size: 16px; /* 根据需要调整 */
    line-height: 1.5;
    color: #FFFFFF; /* 白色文字 */
    background-color: #000; /* 黑色背景 */
  }

  /* 布局和容器 */
  .web, .p-home-rules, .p-home-bench, .p-home-banner, .p-home-list, .c-header, .c-footer {
    width: 100%;
    padding: 2rem;
    box-sizing: border-box;
  }

  /* 按钮样式调整 */
  .c-button {
    font-size: 16px; /* 字体大小 */
    border-radius: 15px; /* 圆角 */
    transition: background-color 0.3s; /* 过渡效果 */
  }

  .c-button:hover {
    background-color: #E6B800; /* 悬停时背景色 */
  }

  /* 头部样式调整 */
  .c-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem;
    background-color: transparent; /* 透明背景 */
  }

  .c-header .logo a {
    width: 8rem;
    height: 3rem;
  }

  /* 横幅样式调整 */
  .p-home-banner {
    padding: 0.2rem;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
  }

  .p-home-banner img {
    width: 80%;
    height: auto;
  }

  /* 工作台样式调整 */
  .p-home-bench main {
    padding-top: 8.5rem;
    height: 27rem;
    background-size: cover;
    background-position: center;
  }

  .p-home-bench main h3 {
    font-size: 20px; /* 根据需要调整 */
  }

  .p-home-bench main em {
    font-size: 2rem;
    margin: .2rem 0 2.5rem;
    color: #FFE7AF; /* 黄色文字 */
    text-shadow: -1px -1px 0 #000, -2px -2px 0 #FFE7AF; /* 文字阴影 */
    text-transform: uppercase;
  }

  .p-home-bench main button {
    width: 100%;
    height: 50px; /* 根据需要调整 */
    font-size: 16px; /* 字体大小 */
    padding-bottom: 0;
  }

  /* 规则区域样式调整 */
  .p-home-rules article {
    padding: 20px;
    background-size: cover;
    background-position: center;
    font-size: .5rem;
  }

  .p-home-rules article h3 {
    font-size: 20px; /* 根据需要调整 */
  }

  /* 底部样式调整 */
